"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { toast } from "sonner";
import { TableIcon } from "lucide-react";
import {
  Bar,
  BarChart,
  ResponsiveContainer,
  XAxis,
  YAxis,
  Tooltip,
} from "recharts";

interface StripeData {
  date: string;
  mrr: number;
  customers: number;
  churnRate: string;
}

export function StripeIntegration() {
  const [apiKey, setApiKey] = useState("");
  const [isConnecting, setIsConnecting] = useState(false);
  const [isConnected, setIsConnected] = useState(false);
  const [stripeData, setStripeData] = useState<StripeData[]>([]);
  const [isLoading, setIsLoading] = useState(false);

  const connectStripe = async () => {
    if (!apiKey) {
      toast.error("Veuillez entrer une clé API Stripe");
      return;
    }

    setIsConnecting(true);
    try {
      const response = await fetch("/api/integrations/stripe/connect", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ apiKey }),
      });

      if (!response.ok) {
        const error = await response.json();
        throw new Error(
          error.message || "Erreur lors de la connexion à Stripe"
        );
      }

      // Simulons quelques données pour le moment
      const mockData: StripeData[] = [
        { date: "Jan", mrr: 1200, customers: 12, churnRate: "2.5%" },
        { date: "Fév", mrr: 1400, customers: 14, churnRate: "2.1%" },
        { date: "Mar", mrr: 1800, customers: 18, churnRate: "1.8%" },
        { date: "Avr", mrr: 2200, customers: 22, churnRate: "1.5%" },
        { date: "Mai", mrr: 2600, customers: 26, churnRate: "1.2%" },
        { date: "Juin", mrr: 3000, customers: 30, churnRate: "1.0%" },
      ];

      setStripeData(mockData);
      setIsConnected(true);
      toast.success("Connexion à Stripe réussie!");
    } catch (error) {
      console.error("Erreur de connexion à Stripe:", error);
      toast.error(
        (error as Error).message || "Erreur lors de la connexion à Stripe"
      );
    } finally {
      setIsConnecting(false);
    }
  };

  const refreshData = async () => {
    setIsLoading(true);
    try {
      const response = await fetch("/api/integrations/stripe/data");

      if (!response.ok) {
        const error = await response.json();
        throw new Error(
          error.message || "Erreur lors de la récupération des données Stripe"
        );
      }

      // Simulons quelques données mises à jour
      const updatedMockData = [...stripeData];
      updatedMockData[updatedMockData.length - 1].mrr += 200;
      updatedMockData[updatedMockData.length - 1].customers += 2;

      setStripeData(updatedMockData);
      toast.success("Données Stripe mises à jour!");
    } catch (error) {
      console.error("Erreur de récupération des données:", error);
      toast.error(
        (error as Error).message ||
          "Erreur lors de la récupération des données Stripe"
      );
    } finally {
      setIsLoading(false);
    }
  };

  const formatCurrency = (value: number) => {
    return new Intl.NumberFormat("fr-FR", {
      style: "currency",
      currency: "EUR",
    }).format(value);
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <TableIcon className="h-5 w-5" />
          Stripe MRR
        </CardTitle>
        <CardDescription>
          Connectez votre compte Stripe pour suivre votre MRR et vos clients
        </CardDescription>
      </CardHeader>
      <CardContent>
        {!isConnected ? (
          <div className="space-y-4">
            <div className="space-y-2">
              <p className="text-sm">
                Entrez votre clé API Stripe secrète pour vous connecter
              </p>
              <Input
                type="password"
                placeholder="sk_live_..."
                value={apiKey}
                onChange={(e) => setApiKey(e.target.value)}
              />
            </div>
            <Button
              onClick={connectStripe}
              disabled={isConnecting || !apiKey}
              className="w-full"
            >
              {isConnecting ? "Connexion en cours..." : "Connecter Stripe"}
            </Button>
          </div>
        ) : (
          <div className="space-y-6">
            <div className="flex justify-between items-center">
              <h3 className="text-sm font-medium">MRR</h3>
              <Button
                variant="outline"
                size="sm"
                onClick={refreshData}
                disabled={isLoading}
              >
                {isLoading ? "Chargement..." : "Actualiser"}
              </Button>
            </div>

            <div className="grid grid-cols-3 gap-4">
              <div className="bg-muted/50 p-4 rounded-lg">
                <div className="text-xs text-muted-foreground">MRR actuel</div>
                <div className="text-2xl font-bold mt-1">
                  {stripeData.length > 0
                    ? formatCurrency(stripeData[stripeData.length - 1].mrr)
                    : "€0"}
                </div>
              </div>
              <div className="bg-muted/50 p-4 rounded-lg">
                <div className="text-xs text-muted-foreground">Clients</div>
                <div className="text-2xl font-bold mt-1">
                  {stripeData.length > 0
                    ? stripeData[stripeData.length - 1].customers
                    : "0"}
                </div>
              </div>
              <div className="bg-muted/50 p-4 rounded-lg">
                <div className="text-xs text-muted-foreground">
                  Taux de churn
                </div>
                <div className="text-2xl font-bold mt-1">
                  {stripeData.length > 0
                    ? stripeData[stripeData.length - 1].churnRate
                    : "0%"}
                </div>
              </div>
            </div>

            <div className="h-64 pt-4">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart data={stripeData}>
                  <XAxis dataKey="date" />
                  <YAxis tickFormatter={(value) => `€${value}`} width={50} />
                  <Tooltip
                    formatter={(value) => [
                      formatCurrency(value as number),
                      "MRR",
                    ]}
                    labelFormatter={(value) => `${value}`}
                  />
                  <Bar dataKey="mrr" fill="#10b981" radius={[4, 4, 0, 0]} />
                </BarChart>
              </ResponsiveContainer>
            </div>
          </div>
        )}
      </CardContent>
      {isConnected && (
        <CardFooter className="flex justify-end">
          <Button
            variant="ghost"
            size="sm"
            onClick={() => {
              setIsConnected(false);
              setApiKey("");
              setStripeData([]);
            }}
          >
            Déconnecter
          </Button>
        </CardFooter>
      )}
    </Card>
  );
}
