"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { toast } from "sonner";
import { Share2Icon, BarChart3Icon } from "lucide-react";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Badge } from "@/components/ui/badge";

interface SocialData {
  platform: string;
  username: string;
  followers: number;
  engagement: number;
  reach: number;
  isConnected: boolean;
}

type SocialPlatform =
  | "twitter"
  | "bluesky"
  | "youtube"
  | "instagram"
  | "tiktok";

export function SocialIntegration() {
  const [socialAccounts, setSocialAccounts] = useState<SocialData[]>([
    {
      platform: "twitter",
      username: "",
      followers: 0,
      engagement: 0,
      reach: 0,
      isConnected: false,
    },
    {
      platform: "bluesky",
      username: "",
      followers: 0,
      engagement: 0,
      reach: 0,
      isConnected: false,
    },
    {
      platform: "youtube",
      username: "",
      followers: 0,
      engagement: 0,
      reach: 0,
      isConnected: false,
    },
    {
      platform: "instagram",
      username: "",
      followers: 0,
      engagement: 0,
      reach: 0,
      isConnected: false,
    },
    {
      platform: "tiktok",
      username: "",
      followers: 0,
      engagement: 0,
      reach: 0,
      isConnected: false,
    },
  ]);

  const [selectedPlatform, setSelectedPlatform] =
    useState<SocialPlatform>("twitter");
  const [username, setUsername] = useState("");
  const [token, setToken] = useState("");
  const [isConnecting, setIsConnecting] = useState(false);
  const [isRefreshing, setIsRefreshing] = useState(false);

  const connectSocial = async () => {
    if (!username || !token) {
      toast.error("Veuillez entrer un nom d'utilisateur et un token d'accès");
      return;
    }

    setIsConnecting(true);
    try {
      const response = await fetch(
        `/api/integrations/social/${selectedPlatform}/connect`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            username,
            token,
          }),
        }
      );

      if (!response.ok) {
        const error = await response.json();
        throw new Error(
          error.message ||
            `Erreur lors de la connexion à ${getPlatformLabel(
              selectedPlatform
            )}`
        );
      }

      // Simulons des données de réponse
      const mockFollowers = Math.floor(1000 + Math.random() * 9000);
      const mockEngagement = Math.floor(100 + Math.random() * 900);
      const mockReach = Math.floor(mockFollowers * (2 + Math.random() * 5));

      // Mettre à jour les comptes sociaux
      setSocialAccounts((prevAccounts) =>
        prevAccounts.map((account) =>
          account.platform === selectedPlatform
            ? {
                ...account,
                username,
                followers: mockFollowers,
                engagement: mockEngagement,
                reach: mockReach,
                isConnected: true,
              }
            : account
        )
      );

      toast.success(
        `Connexion à ${getPlatformLabel(selectedPlatform)} réussie!`
      );

      // Réinitialiser les champs
      setUsername("");
      setToken("");
    } catch (error) {
      console.error(`Erreur de connexion à ${selectedPlatform}:`, error);
      toast.error(
        (error as Error).message ||
          `Erreur lors de la connexion à ${getPlatformLabel(selectedPlatform)}`
      );
    } finally {
      setIsConnecting(false);
    }
  };

  const refreshSocialData = async () => {
    setIsRefreshing(true);
    try {
      // Pour chaque compte connecté, actualiser les données
      const updatedAccounts = [...socialAccounts];
      let anySuccess = false;

      for (let i = 0; i < updatedAccounts.length; i++) {
        const account = updatedAccounts[i];
        if (!account.isConnected) continue;

        try {
          const response = await fetch(
            `/api/integrations/social/${account.platform}/data`
          );

          if (response.ok) {
            // Simulons des données actualisées avec une légère augmentation
            updatedAccounts[i] = {
              ...account,
              followers: Math.floor(
                account.followers * (1 + Math.random() * 0.05)
              ),
              engagement: Math.floor(
                account.engagement * (1 + Math.random() * 0.1)
              ),
              reach: Math.floor(account.reach * (1 + Math.random() * 0.08)),
            };
            anySuccess = true;
          }
        } catch (error) {
          console.error(
            `Erreur lors de l'actualisation des données pour ${account.platform}:`,
            error
          );
        }
      }

      if (anySuccess) {
        setSocialAccounts(updatedAccounts);
        toast.success("Données des réseaux sociaux actualisées!");
      } else {
        toast.error("Aucune donnée n'a pu être actualisée.");
      }
    } catch (error) {
      console.error("Erreur lors de l'actualisation des données:", error);
      toast.error(
        "Erreur lors de l'actualisation des données des réseaux sociaux"
      );
    } finally {
      setIsRefreshing(false);
    }
  };

  const disconnectPlatform = (platform: SocialPlatform) => {
    setSocialAccounts((prevAccounts) =>
      prevAccounts.map((account) =>
        account.platform === platform
          ? {
              ...account,
              username: "",
              followers: 0,
              engagement: 0,
              reach: 0,
              isConnected: false,
            }
          : account
      )
    );
    toast.success(`${getPlatformLabel(platform)} déconnecté`);
  };

  const getPlatformLabel = (platform: SocialPlatform): string => {
    switch (platform) {
      case "twitter":
        return "X (Twitter)";
      case "bluesky":
        return "Bluesky";
      case "youtube":
        return "YouTube";
      case "instagram":
        return "Instagram";
      case "tiktok":
        return "TikTok";
      default:
        return platform;
    }
  };

  const getPlatformIcon = (platform: SocialPlatform): React.ReactNode => {
    // Simulons des icônes avec des emojis pour simplifier
    switch (platform) {
      case "twitter":
        return "𝕏";
      case "bluesky":
        return "🟦";
      case "youtube":
        return "▶️";
      case "instagram":
        return "📸";
      case "tiktok":
        return "🎵";
      default:
        return "🔗";
    }
  };

  const getConnectedAccountsCount = () => {
    return socialAccounts.filter((account) => account.isConnected).length;
  };

  const getTotalFollowers = () => {
    return socialAccounts
      .filter((account) => account.isConnected)
      .reduce((sum, account) => sum + account.followers, 0);
  };

  const getAverageEngagement = () => {
    const connectedAccounts = socialAccounts.filter(
      (account) => account.isConnected
    );
    if (connectedAccounts.length === 0) return 0;

    const totalEngagement = connectedAccounts.reduce(
      (sum, account) => sum + account.engagement,
      0
    );
    return Math.floor(totalEngagement / connectedAccounts.length);
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Share2Icon className="h-5 w-5" />
          Réseaux Sociaux
        </CardTitle>
        <CardDescription>
          Connectez vos réseaux sociaux pour suivre votre audience et votre
          engagement
        </CardDescription>
      </CardHeader>
      <CardContent>
        <Tabs defaultValue="accounts">
          <TabsList className="mb-4">
            <TabsTrigger value="accounts">Comptes</TabsTrigger>
            <TabsTrigger value="connect">Connecter</TabsTrigger>
            <TabsTrigger value="stats">Statistiques</TabsTrigger>
          </TabsList>

          <TabsContent value="accounts">
            <div className="space-y-4">
              {getConnectedAccountsCount() > 0 ? (
                <>
                  <div className="flex justify-between items-center">
                    <h3 className="text-sm font-medium">
                      Comptes connectés ({getConnectedAccountsCount()})
                    </h3>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={refreshSocialData}
                      disabled={isRefreshing}
                    >
                      {isRefreshing ? "Actualisation..." : "Actualiser tout"}
                    </Button>
                  </div>

                  <div className="divide-y">
                    {socialAccounts
                      .filter((account) => account.isConnected)
                      .map((account) => (
                        <div
                          key={account.platform}
                          className="py-3 flex justify-between items-center"
                        >
                          <div className="flex items-center gap-2">
                            <span className="text-lg">
                              {getPlatformIcon(
                                account.platform as SocialPlatform
                              )}
                            </span>
                            <div>
                              <h4 className="text-sm font-medium">
                                {getPlatformLabel(
                                  account.platform as SocialPlatform
                                )}
                              </h4>
                              <p className="text-xs text-muted-foreground">
                                @{account.username}
                              </p>
                            </div>
                          </div>
                          <div className="flex items-center gap-4">
                            <div className="text-right">
                              <div className="text-xs text-muted-foreground">
                                Abonnés
                              </div>
                              <div className="text-sm font-medium">
                                {account.followers.toLocaleString("fr-FR")}
                              </div>
                            </div>
                            <Button
                              variant="ghost"
                              size="icon"
                              onClick={() =>
                                disconnectPlatform(
                                  account.platform as SocialPlatform
                                )
                              }
                            >
                              ✕
                            </Button>
                          </div>
                        </div>
                      ))}
                  </div>
                </>
              ) : (
                <div className="text-center py-6">
                  <p className="text-muted-foreground mb-2">
                    Aucun compte connecté
                  </p>
                  <p className="text-sm">
                    Utilisez l'onglet "Connecter" pour ajouter vos comptes de
                    réseaux sociaux
                  </p>
                </div>
              )}
            </div>
          </TabsContent>

          <TabsContent value="connect">
            <div className="space-y-4">
              <Select
                value={selectedPlatform}
                onValueChange={(value) =>
                  setSelectedPlatform(value as SocialPlatform)
                }
              >
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="Sélectionner un réseau social" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="twitter">X (Twitter)</SelectItem>
                  <SelectItem value="bluesky">Bluesky</SelectItem>
                  <SelectItem value="youtube">YouTube</SelectItem>
                  <SelectItem value="instagram">Instagram</SelectItem>
                  <SelectItem value="tiktok">TikTok</SelectItem>
                </SelectContent>
              </Select>

              <div className="space-y-2">
                <p className="text-sm">Nom d'utilisateur</p>
                <Input
                  placeholder={`Nom d'utilisateur ${getPlatformLabel(
                    selectedPlatform
                  )}`}
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                />
              </div>

              <div className="space-y-2">
                <p className="text-sm">Token d'accès</p>
                <Input
                  type="password"
                  placeholder="Token d'accès"
                  value={token}
                  onChange={(e) => setToken(e.target.value)}
                />
              </div>

              <Button
                onClick={connectSocial}
                disabled={isConnecting || !username || !token}
                className="w-full"
              >
                {isConnecting
                  ? "Connexion..."
                  : `Connecter ${getPlatformLabel(selectedPlatform)}`}
              </Button>
            </div>
          </TabsContent>

          <TabsContent value="stats">
            {getConnectedAccountsCount() > 0 ? (
              <div className="space-y-6">
                <div className="grid grid-cols-3 gap-4">
                  <div className="bg-muted/50 p-4 rounded-lg">
                    <div className="text-xs text-muted-foreground">
                      Total des abonnés
                    </div>
                    <div className="text-2xl font-bold mt-1">
                      {getTotalFollowers().toLocaleString("fr-FR")}
                    </div>
                  </div>
                  <div className="bg-muted/50 p-4 rounded-lg">
                    <div className="text-xs text-muted-foreground">
                      Comptes connectés
                    </div>
                    <div className="text-2xl font-bold mt-1">
                      {getConnectedAccountsCount()}
                    </div>
                  </div>
                  <div className="bg-muted/50 p-4 rounded-lg">
                    <div className="text-xs text-muted-foreground">
                      Engagement moyen
                    </div>
                    <div className="text-2xl font-bold mt-1">
                      {getAverageEngagement().toLocaleString("fr-FR")}
                    </div>
                  </div>
                </div>

                <div className="space-y-4">
                  <h3 className="text-sm font-medium">
                    Répartition par plateforme
                  </h3>
                  <div className="space-y-2">
                    {socialAccounts
                      .filter((account) => account.isConnected)
                      .map((account) => (
                        <div
                          key={account.platform}
                          className="flex items-center justify-between"
                        >
                          <div className="flex items-center gap-2">
                            <span>
                              {getPlatformIcon(
                                account.platform as SocialPlatform
                              )}
                            </span>
                            <span className="text-sm">
                              {getPlatformLabel(
                                account.platform as SocialPlatform
                              )}
                            </span>
                          </div>
                          <div className="flex items-center gap-2">
                            <Badge variant="outline">
                              {account.followers.toLocaleString("fr-FR")}
                            </Badge>
                            <BarChart3Icon className="h-4 w-4 text-muted-foreground" />
                          </div>
                        </div>
                      ))}
                  </div>
                </div>
              </div>
            ) : (
              <div className="text-center py-6">
                <p className="text-muted-foreground mb-2">
                  Aucune statistique disponible
                </p>
                <p className="text-sm">
                  Connectez vos comptes de réseaux sociaux pour voir vos
                  statistiques
                </p>
              </div>
            )}
          </TabsContent>
        </Tabs>
      </CardContent>
    </Card>
  );
}
