"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { toast } from "sonner";
import { RiGithubFill } from "@remixicon/react";

interface GithubRepo {
  id: number;
  name: string;
  description: string | null;
  url: string;
  stars: number;
  forks: number;
  isPrivate: boolean;
}

export function GithubIntegration() {
  const [token, setToken] = useState("");
  const [isConnecting, setIsConnecting] = useState(false);
  const [isConnected, setIsConnected] = useState(false);
  const [repos, setRepos] = useState<GithubRepo[]>([]);
  const [isSyncing, setIsSyncing] = useState(false);

  const connectGithub = async () => {
    if (!token) {
      toast.error("Veuillez entrer un token GitHub");
      return;
    }

    setIsConnecting(true);
    try {
      const response = await fetch("/api/integrations/github/connect", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ token }),
      });

      if (!response.ok) {
        const error = await response.json();
        throw new Error(
          error.message || "Erreur lors de la connexion à GitHub"
        );
      }

      const data = await response.json();
      setIsConnected(true);
      setRepos(data.repos || []);
      toast.success("Connexion à GitHub réussie!");
    } catch (error) {
      console.error("Erreur de connexion à GitHub:", error);
      toast.error(
        (error as Error).message || "Erreur lors de la connexion à GitHub"
      );
    } finally {
      setIsConnecting(false);
    }
  };

  const syncRepos = async () => {
    setIsSyncing(true);
    try {
      const response = await fetch("/api/integrations/github/sync", {
        method: "POST",
      });

      if (!response.ok) {
        const error = await response.json();
        throw new Error(
          error.message || "Erreur lors de la synchronisation des repos"
        );
      }

      const data = await response.json();
      setRepos(data.repos || []);
      toast.success("Synchronisation des repos réussie!");
    } catch (error) {
      console.error("Erreur de synchronisation:", error);
      toast.error(
        (error as Error).message ||
          "Erreur lors de la synchronisation des repos"
      );
    } finally {
      setIsSyncing(false);
    }
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <RiGithubFill className="h-5 w-5" />
          GitHub Integration
        </CardTitle>
        <CardDescription>
          Connectez votre compte GitHub pour importer et afficher vos dépôts
        </CardDescription>
      </CardHeader>
      <CardContent>
        {!isConnected ? (
          <div className="space-y-4">
            <div className="space-y-2">
              <p className="text-sm">
                Entrez un token d&apos;accès personnel GitHub avec les
                permissions &quot;repo&quot;
              </p>
              <Input
                type="password"
                placeholder="ghp_votre_token_github"
                value={token}
                onChange={(e) => setToken(e.target.value)}
              />
            </div>
            <Button
              onClick={connectGithub}
              disabled={isConnecting || !token}
              className="w-full"
            >
              {isConnecting ? "Connexion en cours..." : "Connecter GitHub"}
            </Button>
          </div>
        ) : (
          <div className="space-y-4">
            <div className="flex justify-between items-center">
              <h3 className="text-sm font-medium">
                Vos dépôts ({repos.length})
              </h3>
              <Button
                variant="outline"
                size="sm"
                onClick={syncRepos}
                disabled={isSyncing}
              >
                {isSyncing ? "Synchronisation..." : "Synchroniser"}
              </Button>
            </div>
            <div className="divide-y">
              {repos.length > 0 ? (
                repos.map((repo) => (
                  <div key={repo.id} className="py-3">
                    <div className="flex justify-between items-start">
                      <div>
                        <h4 className="text-sm font-medium">{repo.name}</h4>
                        {repo.description && (
                          <p className="text-xs text-muted-foreground mt-1 line-clamp-2">
                            {repo.description}
                          </p>
                        )}
                      </div>
                      <div className="flex items-center text-xs text-muted-foreground space-x-2">
                        <span>⭐ {repo.stars}</span>
                        <span>🍴 {repo.forks}</span>
                        {repo.isPrivate && <span>🔒</span>}
                      </div>
                    </div>
                  </div>
                ))
              ) : (
                <p className="py-3 text-sm text-muted-foreground">
                  Aucun dépôt trouvé. Cliquez sur &quot;Synchroniser&quot; pour
                  importer vos dépôts.
                </p>
              )}
            </div>
          </div>
        )}
      </CardContent>
      {isConnected && (
        <CardFooter className="flex justify-end">
          <Button
            variant="ghost"
            size="sm"
            onClick={() => {
              setIsConnected(false);
              setToken("");
              setRepos([]);
            }}
          >
            Déconnecter
          </Button>
        </CardFooter>
      )}
    </Card>
  );
}
