"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { toast } from "sonner";
import { BarChartIcon } from "lucide-react";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Line,
  LineChart,
  ResponsiveContainer,
  XAxis,
  YAxis,
  Tooltip,
} from "recharts";

interface AnalyticsData {
  date: string;
  visitors: number;
  pageviews: number;
  source?: string;
}

type AnalyticsType = "plausible" | "umami" | "google_analytics";

export function AnalyticsIntegration() {
  const [selectedAnalytics, setSelectedAnalytics] =
    useState<AnalyticsType>("plausible");
  const [apiKey, setApiKey] = useState("");
  const [siteId, setSiteId] = useState("");
  const [isConnecting, setIsConnecting] = useState(false);
  const [isConnected, setIsConnected] = useState(false);
  const [analyticsData, setAnalyticsData] = useState<AnalyticsData[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [timeRange, setTimeRange] = useState("30d");

  const connectAnalytics = async () => {
    if (!apiKey) {
      toast.error("Veuillez entrer une clé API");
      return;
    }

    if (selectedAnalytics !== "google_analytics" && !siteId) {
      toast.error("Veuillez entrer un ID de site");
      return;
    }

    setIsConnecting(true);
    try {
      const response = await fetch(
        `/api/integrations/${selectedAnalytics}/connect`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            apiKey,
            siteId:
              selectedAnalytics !== "google_analytics" ? siteId : undefined,
          }),
        }
      );

      if (!response.ok) {
        const error = await response.json();
        throw new Error(
          error.message ||
            `Erreur lors de la connexion à ${getLabelForAnalytics(
              selectedAnalytics
            )}`
        );
      }

      // Simulons quelques données pour le moment
      const mockData: AnalyticsData[] = generateMockData(30, selectedAnalytics);

      setAnalyticsData(mockData);
      setIsConnected(true);
      toast.success(
        `Connexion à ${getLabelForAnalytics(selectedAnalytics)} réussie!`
      );
    } catch (error) {
      console.error(`Erreur de connexion à ${selectedAnalytics}:`, error);
      toast.error(
        (error as Error).message ||
          `Erreur lors de la connexion à ${getLabelForAnalytics(
            selectedAnalytics
          )}`
      );
    } finally {
      setIsConnecting(false);
    }
  };

  const refreshData = async () => {
    setIsLoading(true);
    try {
      const response = await fetch(
        `/api/integrations/${selectedAnalytics}/data?timeRange=${timeRange}`
      );

      if (!response.ok) {
        const error = await response.json();
        throw new Error(
          error.message ||
            `Erreur lors de la récupération des données ${getLabelForAnalytics(
              selectedAnalytics
            )}`
        );
      }

      // Simulons quelques données mises à jour en fonction de la période
      const days = timeRange === "7d" ? 7 : timeRange === "30d" ? 30 : 90;
      const mockData: AnalyticsData[] = generateMockData(
        days,
        selectedAnalytics
      );

      setAnalyticsData(mockData);
      toast.success(
        `Données ${getLabelForAnalytics(selectedAnalytics)} mises à jour!`
      );
    } catch (error) {
      console.error("Erreur de récupération des données:", error);
      toast.error(
        (error as Error).message ||
          `Erreur lors de la récupération des données ${getLabelForAnalytics(
            selectedAnalytics
          )}`
      );
    } finally {
      setIsLoading(false);
    }
  };

  const generateMockData = (
    days: number,
    source: AnalyticsType
  ): AnalyticsData[] => {
    const data: AnalyticsData[] = [];
    const today = new Date();

    // Facteur d'échelle selon la source
    const scale =
      source === "google_analytics" ? 5 : source === "plausible" ? 2 : 1;

    for (let i = days - 1; i >= 0; i--) {
      const date = new Date(today);
      date.setDate(date.getDate() - i);

      // Base + bruit aléatoire + tendance croissante légère
      const baseVisitors = 100 * scale;
      const noise = Math.random() * 50 * scale;
      const trend = (i / 10) * scale;

      const visitors = Math.floor(baseVisitors + noise + trend);
      const pageviews = Math.floor(visitors * (1.5 + Math.random() * 0.5));

      data.push({
        date: date.toLocaleDateString("fr-FR", {
          month: "short",
          day: "numeric",
        }),
        visitors,
        pageviews,
        source,
      });
    }

    return data;
  };

  const getLabelForAnalytics = (type: AnalyticsType): string => {
    switch (type) {
      case "plausible":
        return "Plausible";
      case "umami":
        return "Umami";
      case "google_analytics":
        return "Google Analytics";
      default:
        return type;
    }
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <BarChartIcon className="h-5 w-5" />
          Analytiques Web
        </CardTitle>
        <CardDescription>
          Connectez vos outils d'analyse pour suivre les visiteurs et les pages
          vues
        </CardDescription>
      </CardHeader>
      <CardContent>
        {!isConnected ? (
          <div className="space-y-4">
            <Select
              value={selectedAnalytics}
              onValueChange={(value) =>
                setSelectedAnalytics(value as AnalyticsType)
              }
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Sélectionner un service d'analytics" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="plausible">Plausible</SelectItem>
                <SelectItem value="umami">Umami</SelectItem>
                <SelectItem value="google_analytics">
                  Google Analytics
                </SelectItem>
              </SelectContent>
            </Select>

            <div className="space-y-2">
              <p className="text-sm">
                Entrez votre clé API {getLabelForAnalytics(selectedAnalytics)}
              </p>
              <Input
                type="password"
                placeholder="Votre clé API..."
                value={apiKey}
                onChange={(e) => setApiKey(e.target.value)}
              />
            </div>

            {selectedAnalytics !== "google_analytics" && (
              <div className="space-y-2">
                <p className="text-sm">Entrez l'ID de votre site</p>
                <Input
                  placeholder="example.com"
                  value={siteId}
                  onChange={(e) => setSiteId(e.target.value)}
                />
              </div>
            )}

            <Button
              onClick={connectAnalytics}
              disabled={
                isConnecting ||
                !apiKey ||
                (selectedAnalytics !== "google_analytics" && !siteId)
              }
              className="w-full"
            >
              {isConnecting
                ? "Connexion en cours..."
                : `Connecter ${getLabelForAnalytics(selectedAnalytics)}`}
            </Button>
          </div>
        ) : (
          <div className="space-y-6">
            <div className="flex justify-between items-center">
              <Tabs
                defaultValue="30d"
                value={timeRange}
                onValueChange={setTimeRange}
                className="w-[200px]"
              >
                <TabsList>
                  <TabsTrigger value="7d">7j</TabsTrigger>
                  <TabsTrigger value="30d">30j</TabsTrigger>
                  <TabsTrigger value="90d">90j</TabsTrigger>
                </TabsList>
              </Tabs>

              <Button
                variant="outline"
                size="sm"
                onClick={refreshData}
                disabled={isLoading}
              >
                {isLoading ? "Chargement..." : "Actualiser"}
              </Button>
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div className="bg-muted/50 p-4 rounded-lg">
                <div className="text-xs text-muted-foreground">Visiteurs</div>
                <div className="text-2xl font-bold mt-1">
                  {analyticsData.length > 0
                    ? analyticsData
                        .reduce((sum, item) => sum + item.visitors, 0)
                        .toLocaleString("fr-FR")
                    : "0"}
                </div>
              </div>
              <div className="bg-muted/50 p-4 rounded-lg">
                <div className="text-xs text-muted-foreground">Pages vues</div>
                <div className="text-2xl font-bold mt-1">
                  {analyticsData.length > 0
                    ? analyticsData
                        .reduce((sum, item) => sum + item.pageviews, 0)
                        .toLocaleString("fr-FR")
                    : "0"}
                </div>
              </div>
            </div>

            <div className="h-64 pt-4">
              <ResponsiveContainer width="100%" height="100%">
                <LineChart data={analyticsData}>
                  <XAxis
                    dataKey="date"
                    tickLine={false}
                    tick={{ fontSize: 12 }}
                    tickMargin={10}
                  />
                  <YAxis
                    width={40}
                    tickLine={false}
                    tick={{ fontSize: 12 }}
                    tickMargin={10}
                  />
                  <Tooltip />
                  <Line
                    type="monotone"
                    dataKey="visitors"
                    stroke="#3b82f6"
                    strokeWidth={2}
                    dot={false}
                    activeDot={{ r: 6 }}
                  />
                  <Line
                    type="monotone"
                    dataKey="pageviews"
                    stroke="#10b981"
                    strokeWidth={2}
                    dot={false}
                    activeDot={{ r: 6 }}
                  />
                </LineChart>
              </ResponsiveContainer>
            </div>
          </div>
        )}
      </CardContent>
      {isConnected && (
        <CardFooter className="flex justify-between">
          <Select
            value={selectedAnalytics}
            onValueChange={(value) => {
              setSelectedAnalytics(value as AnalyticsType);
              refreshData();
            }}
          >
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Sélectionner un service" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="plausible">Plausible</SelectItem>
              <SelectItem value="umami">Umami</SelectItem>
              <SelectItem value="google_analytics">Google Analytics</SelectItem>
            </SelectContent>
          </Select>

          <Button
            variant="ghost"
            size="sm"
            onClick={() => {
              setIsConnected(false);
              setApiKey("");
              setSiteId("");
              setAnalyticsData([]);
            }}
          >
            Déconnecter
          </Button>
        </CardFooter>
      )}
    </Card>
  );
}
